'use client';

import { useState, useMemo } from 'react';
import MainLayout from '@/app/components/layout/MainLayout';
import SearchBar from '@/app/components/ui/SearchBar';
import ToolGrid from '@/app/components/tools/ToolGrid';
import toolsData from '@/data/tools.json';
import { Tool } from '@/types';

export default function Home() {
  const [searchQuery, setSearchQuery] = useState('');

  const filteredTools = useMemo(() => {
    if (!searchQuery) return toolsData as Tool[];

    const lowerQuery = searchQuery.toLowerCase();
    return (toolsData as Tool[]).filter((tool) =>
      tool.name.toLowerCase().includes(lowerQuery) ||
      tool.description.toLowerCase().includes(lowerQuery) ||
      tool.category.toLowerCase().includes(lowerQuery) ||
      tool.tags.some(tag => tag.toLowerCase().includes(lowerQuery))
    );
  }, [searchQuery]);

  return (
    <MainLayout>
      <div className="flex flex-col gap-8">
        {/* Header Section */}
        <div className="flex flex-col gap-4">
          <h1 className="text-3xl md:text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-[#e0e0e0] to-[#00ff9f] w-fit font-mono">
            Command Center
          </h1>
          <p className="text-[#6b7280] max-w-xl">
            Navigate the digital frontier. Curated AI tools for developers, designers, and creators.
          </p>
        </div>

        {/* Search */}
        <div className="sticky top-0 z-30 pt-4 pb-4 -mx-4 px-4 bg-[#0a0a0a]/80 backdrop-blur-xl md:static md:bg-transparent md:p-0">
          <SearchBar value={searchQuery} onChange={setSearchQuery} />
        </div>

        {/* Results */}
        <section>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-sm font-semibold text-[#00ff9f] uppercase tracking-wider">
              Available Tools ({filteredTools.length})
            </h2>
          </div>

          <ToolGrid tools={filteredTools} />

          {filteredTools.length === 0 && (
            <div className="text-center py-20 border border-dashed border-white/10 rounded-xl">
              <p className="text-[#6b7280]">System returns 0 results for queries.</p>
              <button
                onClick={() => setSearchQuery('')}
                className="mt-4 text-[#00ff9f] hover:underline text-sm font-mono"
              >
                Reset Parameters
              </button>
            </div>
          )}
        </section>
      </div>
    </MainLayout>
  );
}
